* {
  font-family: 'Roboto', sans-serif;
  font-size: 18px;
}

.App{
  width: 100vw;
  height: 100vh;
  flex: 1;
  display: flex;
}

body{
  margin: 0;
  padding: 0;
  background-color: #87cefa;
  width: 100vw;
  height: 100vh;
  display: flex;
}

.sun {
  background: url("../src/img/sun.gif");
  background-size: 15%;
  height: 40vh;
  width: 100vw;
  background-repeat: no-repeat;
  animation: moveRight 20s linear infinite;
}

.cloud1 {
  background: url("../src/img/cloud1.png");
  position: absolute;
  top: 0;
  background-size: 15%;
  height: 40vh;
  width: 100vw;
  background-repeat: no-repeat;
  animation: moveRight 10s linear infinite;
}

.cloud2 {
  background: url("../src/img/cloud2.png");
  position: absolute;
  top: 10%;
  background-size: 20%;
  height: 40vh;
  width: 100vw;
  background-repeat: no-repeat;
  animation: moveRight 11s linear infinite;
}

.cloud3 {
  background: url("../src/img/cloud3.png");
  position: absolute;
  top: 20%;
  background-size: 20%;
  height: 40vh;
  width: 100vw;
  background-repeat: no-repeat;
  animation: moveRight 12s linear infinite;
}

.cloud4 {
  background: url("../src/img/cloud4.png");
  position: absolute;
  top: 30%;
  background-size: 20%;
  height: 40vh;
  width: 100vw;
  background-repeat: no-repeat;
  animation: moveRight 13s linear infinite;
}

@keyframes moveRight {

  from {
    background-position: -30% 0;
  } to {
    background-position: 150% 0;
  }
  
}

.container {
  top: 50%;
  left: 50%;
  position: absolute;
  transform: translate(-50%, -50%);
  margin: 0 auto;
  width: 256px;
  text-align: center;
  border: 10px solid #101116;
  background: #101116;
  border-radius: 5px;
}

.keypad {
  display: grid;
  grid-template-columns: repeat(4, 1fr);
  grid-auto-rows: minmax(60px, auto);
}

input[type="text"] {
  height: 75px;
  width: 249px;
  background-color: #101116;
  border: 0px;
  color: #ffffff;
  text-align: right;
  font-size: 25px;
  font-weight: 500;
  letter-spacing: 1px;
}

#clear {
  grid-column: 1/3;
  grid-row: 1;
  color: #262834;
}

#result {
  grid-column: 3/5;
  grid-row: 5;
  color: #262834;
}

#backspace {
  color: #262834;
}

button {
  margin: 5px;
  border: none;
  border-radius: 5px;
  cursor: pointer;
  background: #262834;
  color: #ffffff;
  font-weight: 500;
}

input:focus,
input:active,
button:focus,
button:active {
  outline:none;
}

.highlight {
  background: #56cbdb;
}